(window.NewTabLinks = function (Behaviors) {

  "use strict";
  
  function NewTabLinks(elem) {
    Behaviors.init(NewTabLinks, this, arguments)

    this.footerLinks = this.$elem[0].querySelectorAll('.Footer__Links');

    this.footerLinks.forEach(link => {
      if (link.hostname !== window.location.hostname) {
        link.target = '_blank';
        link.rel = 'noreferrer noopener';
      }
    })
  }

  Behaviors.inherit(NewTabLinks, Behaviors.Behavior);
  NewTabLinks.QUERY = '.Footer';

  Behaviors.register_behavior(NewTabLinks);
  
  return NewTabLinks;

  }(window.Behaviors));