(function (root, factory) {
  "use strict";
  if (typeof define === 'function' && define.amd) {
    define("siteheader", ["jquery", "betteroffcanvas"], factory);
  } else {
    // Browser globals
    root.siteheader = factory(root.jQuery, root.betteroffcanvas);
  }
}(this, function ($, betteroffcanvas, ajaxCart, Handlebars) {
  "use strict";
  "feel good";

  function update_scroll() {
    let header = document.querySelector('.site-header');
    let st = window.pageYOffset || document.documentElement.scrollTop;

    if (st > 0) {
      header.classList.add('site-header--sticky');
      console.log('h y')
    } else {
      header.classList.remove('site-header--sticky');
      console.log('h n');
    }
  }

  window.addEventListener('scroll', update_scroll);

  window.addEventListener('DOMContentLoaded', (event) => {
    /* Main navigation dropdown */

    /* Search */
    // document.querySelectorAll('.js-search').forEach(item => {
    //   item.addEventListener('click', function(){
    //     document.querySelector('#search-dd').classList.add('search-dd--active');
    //     document.querySelector('.site-header').classList.add('site-header--open');

    //     setTimeout(function(){
    //       document.querySelector('.search-dd__input').focus();
    //     }, 301);
    //   });
    // });

    // document.querySelector('.search-dd__input').addEventListener('focusout', () => {
    //   document.querySelector('#search-dd').classList.remove('search-dd--active');
    //   document.querySelector('.site-header').classList.remove('site-header--open');
    // });


    /* Hamburger */
    document.querySelector('.js-hamburger').addEventListener('click', function(){
      this.classList.toggle('site-header__hamburger--open');

      this.closest('.site-header').classList.toggle('site-header--open');
      document.querySelector('.nav-mobile').classList.toggle('nav-mobile--active');
    });

    document.querySelectorAll('.nav-mobile-list__item-link').forEach(item => {
      if (item.parentNode.querySelector('.dd-mega-menu-mobile')) {
        item.addEventListener('click', function(e){
          console.log('click');
          e.preventDefault();
          e.stopPropagation();
          if (document.querySelector('.dd-mega-menu-mobile--active')) {
            document.querySelector('.dd-mega-menu-mobile--active').classList.remove('dd-mega-menu-mobile--active');
            item.parentNode.querySelector('.dd-mega-menu-mobile').classList.add('dd-mega-menu-mobile--active');
          }else{
            item.parentNode.querySelector('.dd-mega-menu-mobile').classList.add('dd-mega-menu-mobile--active');
          }
        });
      }
    });
    
  });

}));
