jQuery(document).ready(function () {
    var current_fs, next_fs, previous_fs; 
    var opacity;

    jQuery(".MemberSignIn__NextButton").click(function () {
        current_fs = jQuery(this).parent().parent();
        next_fs = jQuery(this).parent().parent().next();


        jQuery(".MemberSignIn__StepList li").eq(jQuery(".MemberSignIn__FormTabs").index(next_fs)).addClass("active");


        next_fs.show();

        current_fs.animate(
            { opacity: 0 },
            {
                step: function (now) {

                    opacity = 1 - now;

                    current_fs.css({
                        display: "none",
                        position: "relative",
                    });
                    next_fs.css({ opacity: opacity });
                },
                duration: 600,
            }
        );
    });

    jQuery(".MemberSignIn__BackButton").click(function () {
        current_fs = jQuery(this).parent().parent();
        previous_fs = jQuery(this).parent().parent().prev();

        jQuery(".MemberSignIn__StepList li").eq(jQuery(".MemberSignIn__FormTabs").index(current_fs)).removeClass("active");

        previous_fs.show();

        current_fs.animate(
            { opacity: 0 },
            {
                step: function (now) {

                    opacity = 1 - now;

                    current_fs.css({
                        display: "none",
                        position: "relative",
                    });
                    previous_fs.css({ opacity: opacity });
                },
                duration: 600,
            }
        );
    });
});