(window.gridSlider = function ($, Behaviors) {

  "use strict";
  
  function gridSlider(elem) {
    Behaviors.init(gridSlider, this, arguments);

    let gridItems = this.$elem[0].querySelectorAll('.grid-item');
    
    if (gridItems.length > 0) {
      gridItems.forEach(item => {
        let select = item.querySelector('.subs__select-wrapper');
        let buttonPrice = item.querySelector('.js-sub-btn-price');
        let sellingPlan = false;

        item.querySelectorAll('.js-atc').forEach(button => {
          button.addEventListener('click', function(e){
            e.stopPropagation();
            e.preventDefault();
  
            let variantID = this.dataset.id;
            if (sellingPlan) {
              let formData = {
                'items': [{
                  'id': variantID,
                  'quantity': 1,
                  'selling_plan': sellingPlan,
                  'properties': {
          
                  }
                }]
              }
              fetch(window.Shopify.routes.root + 'cart/add.js', {
                method: 'POST',
                headers: {
                  'Content-Type': 'application/json'
                },
                body: JSON.stringify(formData)
              })
              .then(response => {
                  if(response.ok) {
                    document.querySelector('.js-cart').click();
                  }
              })
              .catch((error) => {
                  console.error('Error:', error);
              });
            }else{
              let formData = {
                'items': [{
                  'id': variantID,
                  'quantity': 1,
                  'properties': {
          
                  }
                }]
              };
              fetch(window.Shopify.routes.root + 'cart/add.js', {
                method: 'POST',
                headers: {
                  'Content-Type': 'application/json'
                },
                body: JSON.stringify(formData)
              })
              .then(response => {
                  if(response.ok) {
                    document.querySelector('.js-cart').click();
                  }
              })
              .catch((error) => {
                  console.error('Error:', error);
              });
            }
          });
        });

        item.querySelectorAll('.subs__radio').forEach(child => {
          child.addEventListener('change', function (e) {
            //console.log('change');
            let val = this.value;
    
            if (val == "subscription") {
              select.classList.add('subs__select-wrapper--active');
              let price = this.dataset.price;
              sellingPlan = item.querySelector('.subs__select').value;
              if (buttonPrice) {
                buttonPrice.innerHTML = price;
              }
            }else if (val == "one_time") {
              select.classList.remove('subs__select-wrapper--active');
              let price = this.dataset.price;
              sellingPlan = false;
              if (buttonPrice) {
                buttonPrice.innerHTML = price;
              }
            }
          }); 
        });

        item.querySelectorAll('.subs__select').forEach(child => {
          child.addEventListener('change', function (e) {
            sellingPlan = this.value;
          })
        })
      });
    }
  }

  Behaviors.inherit(gridSlider, Behaviors.Behavior);
  gridSlider.QUERY = '.product__list';

  Behaviors.register_behavior(gridSlider);
  
  return gridSlider;

  }(window.jQuery, window.Behaviors));