(window.bundle = function (Behaviors) {

  "use strict";

  Shopify.queryParams = {};
  
  function bundle(elem) {
    Behaviors.init(bundle, this, arguments)

    let selected = 0;
    let goal = 6;
    let ids = [];
    let itemIndex;
    let arrayLength = ids.length;

    this.$elem[0].querySelectorAll('.js-bundle').forEach(button => {
      button.addEventListener('click', function() {
        if (this.classList.contains('c-bundle__item-btn--active')) {
          console.log('return');
          return;
        }else{
          if (document.querySelector('.c-bundle__item--active')) {
            document.querySelector('.c-bundle__item--active').classList.remove('c-bundle__item--active');
            document.querySelector('.c-bundle__item-btn--active').textContent = "select";
            document.querySelector('.c-bundle__item-btn--active').classList.remove('c-bundle__item-btn--active');
          }

          this.closest('.c-bundle__item').classList.toggle('c-bundle__item--active');
          this.classList.toggle('c-bundle__item-btn--active');
          this.textContent = "selected";

          goal = this.dataset.select;
          let current = document.querySelectorAll('.c-bundle-footer__checkmark').length;
          let diff = goal - current;

          document.querySelectorAll('.c-bundle-footer__number').forEach(item => {
            item.textContent = goal;
          });

          //document.querySelector('.c-bundle-footer__checkmarks').innerHTML = '';
          if (diff > 0) {
            for(let i = 0; i < diff; i += 1) {
              let el = document.createElement('span');
              el.classList.add('c-bundle-footer__checkmark');
              document.querySelector('.c-bundle-footer__checkmarks').appendChild(el);
            }
          }

          if (diff < 0) {
            diff *= -1;
            for(let i = 0; i < diff; i += 1) {
              let el = document.querySelector('.c-bundle-footer__checkmarks');
              el.removeChild(el.lastElementChild);
            }
          }

          if (arrayLength == goal) {
            document.querySelector('.c-bundle-footer__btn').classList.add('c-bundle-footer__btn--active');
            let price = 0;
            
            document.querySelectorAll('.btn--selected').forEach(item => {
              price = (parseInt(price) + parseInt(item.dataset.price));
            });
            var symbol = theme.moneyFormat.split('{')[0];
            if (symbol == "" || symbol == undefined) {
              console.log('empty');
              document.querySelector('.js-add-bundle-price').textContent = price.toLocaleString(undefined, {minimumFractionDigits: 2, maximumFractionDigits: 2}) + ' ' + Shopify.currency.active;
            }else{
              document.querySelector('.js-add-bundle-price').textContent = symbol + price.toLocaleString(undefined, {minimumFractionDigits: 2, maximumFractionDigits: 2});
            }
          }else{
            document.querySelector('.c-bundle-footer__btn').classList.remove('c-bundle-footer__btn--active');
          }
          
        }
      })
    });
    
    document.querySelectorAll('.js-bundle-atc').forEach(button => {
      button.addEventListener('click', function(){
        let id = this.dataset.id;
        
        if (this.classList.contains('btn--selected')) {
          this.classList.remove('btn--selected');
          this.querySelector('.pdp__btn-text').textContent = "select";
          itemIndex = ids.length;
          ids = ids.filter(e => e !== id);
          if (document.querySelector(`.c-bundle-footer__checkmark:nth-child(${itemIndex})`)) {
            document.querySelector(`.c-bundle-footer__checkmark:nth-child(${itemIndex})`).classList.remove('c-bundle-footer__checkmark--active');
          }
          arrayLength = ids.length;
          //console.log(ids);
        }else{
          if (arrayLength < goal) {
            this.classList.add('btn--selected');
            this.querySelector('.pdp__btn-text').textContent = "selected";
            ids.push(id);
            itemIndex = ids.length;
            arrayLength = ids.length;
            document.querySelector(`.c-bundle-footer__checkmark:nth-child(${itemIndex})`).classList.add('c-bundle-footer__checkmark--active');
            console.log(ids);
          }
        }

        // console.log('arrayLenght: ' + arrayLength);
        // console.log('goal ' + goal);

        if (arrayLength == goal) {
          document.querySelector('.c-bundle-footer__btn').classList.add('c-bundle-footer__btn--active');
          let price = 0;
          
          document.querySelectorAll('.btn--selected').forEach(item => {
            price = (parseInt(price) + parseInt(item.dataset.price));
          });
          var symbol = theme.moneyFormat.split('{')[0];
          if (symbol == "" || symbol == undefined) {
            console.log('empty');
            document.querySelector('.js-add-bundle-price').textContent = price.toLocaleString(undefined, {minimumFractionDigits: 2, maximumFractionDigits: 2}) + ' ' + Shopify.currency.active;
          }else{
            document.querySelector('.js-add-bundle-price').textContent = symbol + price.toLocaleString(undefined, {minimumFractionDigits: 2, maximumFractionDigits: 2});
          }
        }else{
          document.querySelector('.c-bundle-footer__btn').classList.remove('c-bundle-footer__btn--active');
        }
      });
    })

    document.querySelector('.js-add-bundle').addEventListener('click', function() {
      let formData = {
        'items': [
          
        ]
      };

      let items = formData.items;
      let discount = document.querySelector('.c-bundle__item-btn--active').dataset.discount + '%';

      ids.forEach(id => {
        let item = {
          'id': id,
          'quantity': 1,
          'properties': {
            'Discount': discount
          }
        };
    
        items.push(item);
      });

      console.log(items);

      fetch(window.Shopify.routes.root + 'cart/add.js', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify(formData)
      })
      .then(response => {
          if(response.ok) {
            document.querySelector('.js-cart').click();
          }
      })
      .catch((error) => {
          console.error('Error:', error);
      });
    })
  }

  Behaviors.inherit(bundle, Behaviors.Behavior);
  bundle.QUERY = '.c-bundle';

  Behaviors.register_behavior(bundle);
  
  return bundle;

  }(window.Behaviors));