(window.TestimonialSliderPeople = function (Behaviors) {

  "use strict";
  
  function TestimonialSliderPeople(elem) {
    Behaviors.init(TestimonialSliderPeople, this, arguments);

    this.mainSlider = this.$elem[0].querySelector('.testimonial-slide-p__gallery');

    const mainSlider = new Swiper(this.mainSlider, {
      wrapperClass: 'testimonial-slide-p__track',
      slideClass: 'testimonial-slide-p__item',
      spaceBetween: 40,
      speed: 400,
      effect: 'fade',
      allowTouchMove: false,
    });

    document.querySelectorAll('.testimonial-slide-p__item-thumb').forEach(item => {
      item.addEventListener('click', function(){
        let index = this.dataset.index;

        document.querySelector('.testimonial-slide-p__image--active').classList.remove('testimonial-slide-p__image--active');
        
        document.querySelector(`.testimonial-slide-p__image[data-index="${index}"]`).classList.add('testimonial-slide-p__image--active');

        document.querySelectorAll('.testimonial-slide-p__item-thumb--active').forEach(item => {
          item.classList.remove('testimonial-slide-p__item-thumb--active');
        });

        document.querySelectorAll(`.testimonial-slide-p__item-thumb[data-index="${index}"]`).forEach(item => {
          item.classList.add('testimonial-slide-p__item-thumb--active');
        });

        let indexSlider = index - 1;
        mainSlider.slideTo(indexSlider, 400);
      });
    });

  }

  Behaviors.inherit(TestimonialSliderPeople, Behaviors.Behavior);
  TestimonialSliderPeople.QUERY = '.js-testimonial-slider-people';

  Behaviors.register_behavior(TestimonialSliderPeople);
  
  return TestimonialSliderPeople;

  }(window.Behaviors));