(window.MobileDropdown = function (Behaviors) {

  "use strict";
  
  function MobileDropdown(elem) {
    Behaviors.init(MobileDropdown, this, arguments);
    this.dropdown = this.$elem[0];
    this.items = this.dropdown.querySelectorAll('.nav-mobile-list__item--has-dd');
    this.items.forEach(item => {
      item.addEventListener('click', (e)=>{
        console.log('click');
        e.stopPropagation();
        e.preventDefault();
        this.slideAnim(e);
      });
    })
  } 

  Behaviors.inherit(MobileDropdown, Behaviors.Behavior);
  MobileDropdown.QUERY = ".nav-mobile";

  MobileDropdown.prototype.slideAnim = function(e) {
    this.el = e.currentTarget;
    this.body = this.el.querySelector('.dd-mega-menu-mobile');
    //console.log(this.body);
    this.active = document.querySelector('.nav-mobile-list__item--active');
    if (this.active) {
      this.active.classList.remove('nav-mobile-list__item--animate');
      this.active.querySelector('.dd-mega-menu-mobile').style.height = "0px";
      this.active.querySelector('.dd-mega-menu-mobile').addEventListener('transitionend', () => {
        this.active.classList.remove('nav-mobile-list__item--active')
      }, { once: true })
    }
    if (!this.el.classList.contains('nav-mobile-list__item--active')) {
      console.log('active');
      this.el.classList.add('nav-mobile-list__item--active', 'nav-mobile-list-item--animate')
      this.body.style.height = 'auto';
      this.height = this.body.offsetHeight + 'px';
      this.body.style.height = "0px";
      setTimeout(() => {
        this.body.style.height = this.height;
      }, 0);
    } else {
      this.el.classList.remove('nav-mobile-list-item--animate');
      this.body.style.height = "0px";
      this.body.addEventListener('transitionend', () => {
        this.el.classList.remove('nav-mobile-list__item--active');
      }, { once: true })
    }
  }

  Behaviors.register_behavior(MobileDropdown);

  return MobileDropdown;

  }(window.Behaviors));