(window.ImageGrid = function (Behaviors) {

  "use strict";
  
  function ImageGrid(elem) {
    Behaviors.init(ImageGrid, this, arguments);

    this.ImageGridSlider = this.$elem[0];

    if(window.innerWidth < 1025) {
      const slider = new Swiper(this.ImageGridSlider, {
        spaceBetween: 40,
        wrapperClass: 'image-grid__list',
        slideClass: 'image-grid__list-item',
        slidesPerView: 'auto',
        speed: 500,
        slideToClickedSlide: true
      });
    }
  }

  Behaviors.inherit(ImageGrid, Behaviors.Behavior);
  ImageGrid.QUERY = '.js-image-grid';

  Behaviors.register_behavior(ImageGrid);
  
  return ImageGrid;

  }(window.Behaviors));