(window.filterExpand = function (Behaviors) {

  "use strict";

  Shopify.queryParams = {};
  
  function filterExpand(elem) {
    Behaviors.init(filterExpand, this, arguments)

    this.filterButton = this.$elem[0];
    this.filterButton.addEventListener("click", function() {
      this.parentNode.querySelector('.collection-main__filter').classList.toggle('collection-main__filter--active');
    });

    document.querySelectorAll('.js-filter-tag input[type="checkbox"]').forEach(item => {
      item.addEventListener("change", function () {
        let tag = this.value;
        let baseUrl = document.querySelector('body').dataset.collection;

        if (item.checked) {
          if (tag) {
            window.location = baseUrl + '/' + tag;
          }
        }else{
          window.location = baseUrl;
        }
      });
    });

    document.querySelectorAll('.js-sort input[type="checkbox"]').forEach(item => {
      item.addEventListener("change", function () {
        let sortValue = this.value;
        console.log(sortValue);

        Shopify.queryParams.sort_by = sortValue;
        location.search = new URLSearchParams(Shopify.queryParams).toString();
      });
    })
  }

  Behaviors.inherit(filterExpand, Behaviors.Behavior);
  filterExpand.QUERY = '.js-collection-filter';

  Behaviors.register_behavior(filterExpand);
  
  return filterExpand;

  }(window.Behaviors));