(window.ProductSlider = function (Behaviors, $) {

  "use strict";
  
  function ProductSlider(elem) {
    Behaviors.init(ProductSlider, this, arguments);

    this.container = $(this.$elem[0]);

    if ($(window).width() <= 1024){	
      this.container.find(".AnchorLinks__Dropdown").click((e)=> {
        this.container.find(".AnchorLinks__List").slideToggle();
      });

      this.container.find(".AnchorLinks__List-title").click((e)=> {
        var btnText = $(e.currentTarget).text();
        this.container.find(".AnchorLinks__List").slideToggle();
        this.container.find(".AnchorLinks__Dropdown-btn").text(btnText);
      });
    }	else {
      this.container.find(".AnchorLinks__List-title").click((e)=> {
        if (this.container.find('.AnchorLinks__List-title--active')) {
          this.container.find('.AnchorLinks__List-title--active').removeClass('AnchorLinks__List-title--active');
        }
        e.currentTarget.classList.add('AnchorLinks__List-title--active');
      });
    }
  }

  Behaviors.inherit(ProductSlider, Behaviors.Behavior);
  ProductSlider.QUERY = '.AnchorLinks';

  Behaviors.register_behavior(ProductSlider);
  
  return ProductSlider;

  }(window.Behaviors, window.jQuery));