(window.NavigationInner = function (Behaviors) {

  "use strict";
  
  function NavigationInner(elem) {
    Behaviors.init(NavigationInner, this, arguments);

    this.$elem[0].querySelectorAll('.nav-inner__list-item').forEach(item => {
      item.addEventListener('click', (e)=>{
        this.$elem[0].querySelector('.nav-inner__list-item--active').classList.remove('nav-inner__list-item--active');
        e.currentTarget.classList.add('nav-inner__list-item--active');
      });
    })
  }

  Behaviors.inherit(NavigationInner, Behaviors.Behavior);
  NavigationInner.QUERY = '.nav-inner';

  Behaviors.register_behavior(NavigationInner);
  
  return NavigationInner;

  }(window.Behaviors)); 