(window.TitleGrid = function (Behaviors) {

  "use strict";
  
  function TitleGrid(elem) {
    Behaviors.init(TitleGrid, this, arguments);

    this.TitleGridSlider = this.$elem[0];

    if(window.innerWidth < 768) {
      const slider = new Swiper(this.TitleGridSlider, {
        spaceBetween: 40,
        wrapperClass: 'title-grid__list',
        slideClass: 'title-grid__list-item',
        slidesPerView: 'auto',
        speed: 500,
        slideToClickedSlide: true
      });
    }
  }

  Behaviors.inherit(TitleGrid, Behaviors.Behavior);
  TitleGrid.QUERY = '.js-title-grid';

  Behaviors.register_behavior(TitleGrid);
  
  return TitleGrid;

  }(window.Behaviors));