(window.TestimonialSlider = function (Behaviors) {

  "use strict";
  
  function TestimonialSlider(elem) {
    Behaviors.init(TestimonialSlider, this, arguments);

    this.mainSlider = this.$elem[0].querySelector('.testimonial-slider__copy-gallery');
    this.thumbSlider = this.$elem[0].querySelector('.testimonial-slider__gallery');

    const thumbSlider = new Swiper(this.thumbSlider, {
      wrapperClass: 'testimonial-slider__track',
      slideClass: 'testimonial-slider__item',
      watchSlidesProgress: true,
      speed: 400,
      breakpoints: {
        320: {
          slidesPerView: 'auto',
          centeredSlides: true,
          spaceBetween: 40,
          loop: true
        },
        768: {
          slidesPerView: 'auto',
          spaceBetween: 0,
        }
      }
    });

    const mainSlider = new Swiper(this.mainSlider, {
      wrapperClass: 'testimonial-slider__copy-track',
      slideClass: 'testimonial-slider__copy',
      spaceBetween: 40,
      speed: 400,
      effect: 'fade',
      allowTouchMove: false,
      thumbs: {
        swiper: thumbSlider
      }
    });

    thumbSlider.on('slideChange', function (e) {
      let index = thumbSlider.realIndex;
      mainSlider.slideTo(index, 400);
    });

    //thumbSlider.controller.control = mainSlider;
    // mainSlider.controller.control = thumbSlider; 

    // document.querySelectorAll('.testimonial-slider__item').forEach(item => {
    //   item.addEventListener('click', function(){
    //     document.querySelector('.testimonial-slider__item--active').classList.remove('testimonial-slider__item--active');
    //     this.classList.add('testimonial-slider__item--active');
    //   });
    // });
  }

  Behaviors.inherit(TestimonialSlider, Behaviors.Behavior);
  TestimonialSlider.QUERY = '.js-testimonial-slider';

  Behaviors.register_behavior(TestimonialSlider);
  
  return TestimonialSlider;

  }(window.Behaviors));