(window.HeroParallax = function (Behaviors) {

  "use strict";
  
  function HeroParallax(elem) {
    Behaviors.init(HeroParallax, this, arguments);

    // Contains the items you want to parallax.
    const parallaxContainer = document.querySelector('.hero');
  
    // The elements you want to parallax.
    let img1 = parallaxContainer.querySelector('.hero__image-part-1');
    let img2 = parallaxContainer.querySelector('.hero__image-part-2');
    let img3 = parallaxContainer.querySelector('.hero__image-part-3');
    let img4 = parallaxContainer.querySelector('.hero__image-part-4');
    let img5 = parallaxContainer.querySelector('.hero__image-part-5');
    let img6 = parallaxContainer.querySelector('.hero__image-part-6');
     
    parallaxContainer.addEventListener("mousemove", function(e){
      parallaxIt(e, img1, -75);
      parallaxIt(e, img2, -60);
      parallaxIt(e, img3, -25);
      parallaxIt(e, img4, -45);
      parallaxIt(e, img5, -35);
      parallaxIt(e, img6, -15);
    });

    function parallaxIt(e, target, movement) {
      var relX = e.pageX - parallaxContainer.offsetLeft;
      var relY = e.pageY - parallaxContainer.offsetTop;
    
      gsap.to(target, 1, {
        x: (relX - parallaxContainer.offsetWidth / 2) / parallaxContainer.offsetWidth * movement,
        y: (relY - parallaxContainer.offsetHeight / 2) / parallaxContainer.offsetHeight * movement
      });
    }
  }

  Behaviors.inherit(HeroParallax, Behaviors.Behavior);
  HeroParallax.QUERY = '.js-parallax-hero';

  Behaviors.register_behavior(HeroParallax);
  
  return HeroParallax;

  }(window.Behaviors));
