(window.PDPaction = function ($, Behaviors) {

  "use strict";
  
  function PDPaction(elem) {
    Behaviors.init(PDPaction, this, arguments)

    let select = this.$elem[0].querySelector('.subs__select-wrapper');
    let buttonPrice = this.$elem[0].querySelector('.js-sub-btn-price');
    let sellingPlan = false;

    this.$elem[0].querySelectorAll('.subs__radio').forEach(item => {
      item.addEventListener('change', function () {
        let val = this.value;

        if (val == "subscription") {
          select.classList.add('subs__select-wrapper--active');
          let price = this.dataset.price;
          sellingPlan = select.querySelector('.subs__select').value;
          if (buttonPrice) {
            buttonPrice.innerHTML = price;
          }
        }else if (val == "one_time") {
          select.classList.remove('subs__select-wrapper--active');
          let price = this.dataset.price;
          sellingPlan = false;
          if (buttonPrice) {
            buttonPrice.innerHTML = price;
          }
        }
      }); 
    });

    this.$elem[0].querySelectorAll('.subs__select').forEach(child => {
      child.addEventListener('change', function (e) {
        sellingPlan = this.value;
      })
    })

    this.$elem[0].querySelectorAll('.js-qty-btn').forEach(item => {
      item.addEventListener('click', function(){
        let currentValue = document.querySelector('.js-pdp__quantity').value;
        let updatedValue;
        let operator = this.dataset.attr;
        switch(operator) {
          case '+':
            updatedValue = ++currentValue;
            break;
          case '-':
            updatedValue = currentValue == 1 ? 1 : --currentValue;
            break;  
        }
        document.querySelector('.js-pdp__quantity').value = updatedValue;
        document.querySelector('.js-pdp__quantity-input').innerHTML = updatedValue;
      })
    });

    if (document.querySelector('.js-sub-submit')) {
      document.querySelector('.js-sub-submit').addEventListener('click', function(){
        let variantID = this.dataset.id;
        let quantity = document.querySelector('.js-pdp__quantity-input').textContent;

        if (sellingPlan) {
          let formData = {
            'items': [{
              'id': variantID,
              'quantity': quantity,
              'selling_plan': sellingPlan,
              'properties': {
      
              }
            }]
          }
          fetch(window.Shopify.routes.root + 'cart/add.js', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify(formData)
          })
          .then(response => {
              if(response.ok) {
                document.querySelector('.js-cart').click();
              }
          })
          .catch((error) => {
              console.error('Error:', error);
          });
        }else{
          let formData = {
            'items': [{
              'id': variantID,
              'quantity': 1,
              'properties': {
      
              }
            }]
          };
          fetch(window.Shopify.routes.root + 'cart/add.js', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify(formData)
          })
          .then(response => {
              if(response.ok) {
                document.querySelector('.js-cart').click();
              }
          })
          .catch((error) => {
              console.error('Error:', error);
          });
        }
      });
    }
  }

  Behaviors.inherit(PDPaction, Behaviors.Behavior);
  PDPaction.QUERY = '.pdp__inner';

  Behaviors.register_behavior(PDPaction);
  
  return PDPaction;

  }(window.jQuery, window.Behaviors));