(window.ProductSlider = function (Behaviors) {

  "use strict";
  
  function ProductSlider(elem) {
    Behaviors.init(ProductSlider, this, arguments);

    this.pSlider = this.$elem[0];

    const pSlider = new Swiper(this.pSlider, {
      wrapperClass: 'product-slider__track',
      slideClass: 'product-slider__item',
      slidesPerView: 'auto',
      speed: 500,
      scrollbar: {
        el: '.product-slider__scrollbar',  
        draggable : true,
        snapOnRelease : true,
      },
      mousewheel: {
        forceToAxis: true,
        invert: true,
        releaseOnEdges: false
      },
      runCallbacksOnInit: true,
      observer: true,
      breakpoints: {
        // when window width is >= 320px
        320: {
          spaceBetween: 15,
        },
        1025: {
          spaceBetween: 35
        }
      }
    });

    document.querySelectorAll('.js-atc-slide').forEach(item => {
      item.addEventListener('click', function(){
        let variantID = this.dataset.variant;
        let formData = {
          'items': [{
            'id': variantID,
            'quantity': 1
          }]
         };
         fetch(window.Shopify.routes.root + 'cart/add.js', {
            method: 'POST',
            headers: {
              'Content-Type': 'application/json'
            },
            body: JSON.stringify(formData)
         })
         .then(response => {
            if(response.ok) {
              document.querySelector('.js-cart').click();
            }
         })
         .catch((error) => {
            console.error('Error:', error);
            console.log('works');
         });
      });
    })
  }

  Behaviors.inherit(ProductSlider, Behaviors.Behavior);
  ProductSlider.QUERY = '.js-product-slider-gallery';

  Behaviors.register_behavior(ProductSlider);
  
  return ProductSlider;

  }(window.Behaviors));